<template>
  <div class="message-wrapper">
      <div class="message-container" :class="[closing ? 'slidedown' : 'slideUp']">
          <div class="nav-menu" :class="[closing ? 'fadeOut' : 'fadeIn']">
         <i class="fa fa-envelope  fa-lg" aria-hidden="true"></i>
         <span style="font-weight:bold">Messages</span>
         <i @click="closeMessage" style="position:absolute;right: 10%;" class="fa fa-times fa-lg" aria-hidden="true"></i>
        
      </div>
       <div class="matched-container"  :class="[closing ? 'match-fadeOut' : 'match-fadeIn']">
           <span class="matched-label">MATCHED</span>           
            <avatar  v-for="user in matched" :user="user"></avatar>
        </div>
        <div v-show="isChatBoxOpen" class="chat-box" :class="[closing ? 'chat-fadeOut' : 'chat-fadeIn']">
             <div style="height:  180px;"></div>
             <div v-show="!messages">
                 hahhah
             </div>
             <div class="match-message" v-for="(message , key , index) in messages" >
                 <div class="chat-avatar" v-bind:class="[user._id === message.from ? 'user' : '']">
                 <span v-show="user._id === message.from">
                      <span>
                          <img alt="" v-if="messages[0]._id != message._id && messages[key-1].from != user._id " v-bind:src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + user.image" v-show="user.image">
                      </span>
                      
                 </span>
                 <span v-show="user._id != message.from">
                     <img  alt="" v-if="messages[0]._id != message._id && messages[key-1].from === user._id " v-bind:src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + messageAvatar" v-show="messageAvatar">
                 </span>

                  <img style="position:relative;margin-left: 5px;top:-20px;height:20px;width: 20px;" src="https://res.cloudinary.com/dxwtipc8n/image/upload/q_50/male-avatar_illsec.svg" alt="" v-show="!user.image || !messageAvatar">
                 </div>
                 <span class="line-message" v-bind:class="[user._id === message.from ? 'user message-user-bg' : '']"> {{message.message}}</span>
             </div>          
           

             <div id="#endpoint" style="height: 160px;"></div>

              <div class="chat-input"  :class="[closing ? 'input-fadeOut' : 'input-fadeIn']">
                    <input type="text" v-model="message">
                    <button @click="sendMessage(message)"><i class="fa fa-paper-plane fa-lg" aria-hidden="true"></i></button>
            </div>
      </div>
    

      </div>

      
  </div>
</template>
<script>
import {Bus} from '../main'
import {mapGetters} from 'vuex';
import avatar from './avatar.vue'
export default {
  name : 'messages',
  data () {
     return{
          closing : true,
          scrollPoint : '',
          isChatBoxOpen : false,
          messageAvatar : '',
          message : '',
          userId : '',
          oldUser : '',
          avatarUser : '',
         
     }
  },
  components :{
      avatar
  },
  methods : {
      closeMessage() {
         
         this.closing = true
         setTimeout(()=>{
             Bus.$emit('closeMessage')
         } , 500)
      },
      getMessages(userId , userImage){
         
         
           
      },
      sendMessage(msg){
            this.message = ''
          if(msg){
              this.$socket.emit('sendMessage' , this.$store.state.user._id , this.userId ,msg);
          }
          
      },
      checkIfUserAgain : function(user){
          if(this.avatarUser === user){
              return false
          }else{
              this.avatarUser = user
              return true
          }
      }

     
     
  },
  computed : {
      ...mapGetters({
          matched : 'matched',
          messages : 'messages',
          user : 'user',
          newMessages : 'newMessages'
      }),
     
  },
  mounted : function(){
      Bus.$on('openMessage' , ()=>{      
          this.closing = false
      })
     Bus.$on('messages' , ()=>{
     
            var elem = this.$el.children[0].children[2]          
            elem.scrollTop = elem.scrollHeight ;
     
     })
       Bus.$on('messageSuccess' , ()=>{
            
              var elem = this.$el.children[0].children[2]          
                elem.scrollTop = elem.scrollHeight * 2;
               
          })

   

    Bus.$on('getMessages' , (userId , userImg)=>{
        
         if(userId != this.oldUser ){
              this.$store.state.messages = []
               this.userId = userId
               this.$socket.emit('getMessages' , this.$store.state.user._id , userId )
               this.isChatBoxOpen = true
                this.messageAvatar = userImg
                this.oldUser = userId

          }else{
              console.log('old')
              this.oldUser = userId
          }

          this.$store.state.chatActive = userId
    })



    var elem = this.$el
    
    
    
     
  }
}
</script>
<style scoped>
.message-container{
    position: fixed;
    z-index: 9999;
    width: 100%;
    height: 100%;    
    top:100%;
    background-color :#fff;
    font-size: 1em;
    overflow: hidden;
     box-shadow: 0px 0px 30px rgba(0,0,0,0.2);
    
}
.nav-menu{
    position: fixed;
    top: 100%;
    width: 100%;
    opacity: 0;    
    color : #e02100;
    padding-top: 10px;
    padding-left: 20px;
    height: 40px;
    z-index: 30;
    background-color :#fff;
    border-bottom: 1px solid #e02100;
    
}
.matched-container{
    z-index: 20;
    background-color :#fff;
    position: fixed;
    top: 50px;
    padding-top: 10px;
    height: 70px;
    width: 100%;
   white-space: nowrap;
   overflow:auto;
      box-shadow: 0px 0px 30px rgba(0,0,0,0.2);
 
}
.input-fadeIn{
   bottom: 0;
     transition: all  0.3s ease-in-out;
    transition-delay: 0.3s;
}
.input-fadeOut{
    margin-bottom: 500%;
    transition: all  0.1s ease-in-out;
    transition-delay: 0;
}
.chat-fadeIn{   
    
    top: 0%;
    transition: all  0.3s ease-in-out;
    transition-delay: 0.3s;
}
.chat-fadeOut{    
    
    top: 100%;
    transition: all  0.1s ease-in-out;
    transition-delay: 0;
}
.slideUp{
     transition: all 0.3s ease-in-out;
   top: 0%;
}
.slidedown{
   transition: all 0.3s ease-in-out;
   transition-delay: 0.1s;
   top:100%;
    
    
}
.match-fadeIn{
  
    transition: all  0.3s ease-in-out;
    transition-delay: 0.3s;
}
.match-fadeOut{
    opacity: 0;
    top: 100%;
    transition: all  0.1s ease-in-out;
    transition-delay: 0;
}
.fadeIn{
    opacity: 1;
    top: 0%;
    transition: all  0.3s ease-in-out;
    transition-delay: 0.3s;
}
.fadeOut{
    opacity : 0;
    top: 100%;
    transition: all  0.1s ease-in-out;
    transition-delay: 0;
}

.matched-label{
    position: absolute;
    font-size: 0.1em;
    font-weight: bolder;
    letter-spacing: 8px;
    top: 10px;
    left: 15px;
    color : #e02100;
}
.chat-box{
    display:block;
    position: fixed;  
    width: 99%;
    height: 100%;
    bottom: 35px;
    z-index: 0;
    
    overflow-y:scroll;
    overflow-x:hidden;
    color :#fff;
    font-size: 1em;
    padding: 5px;
   
    
}
.chat-avatar{
    overflow: hidden;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    display:inline-block;
    position: relative;
    vertical-align: top;
    top: 8px;

}
.chat-avatar img{
    
    width: 100%;
}
.match-message{
    display:block;    
    width: 99%;
   
    word-wrap: break-word;
    clear: both;    
    padding:5px;
    vertical-align: text-top;
   
}
.line-message{
    background-color : #eee;
    padding: 10px;
    padding-left: 10px;
    padding-right: 10px;
    max-width: 60%;
    border-radius: 20px;
    position: relative;
    margin-right: 10px;
    margin-left: 10px;
    display:inline-block;
    color :#333;
    
}
.user{
    float:right;
    
    color :#fff;
    margin-right: 10px;
}
.message-user-bg{
    background-color :#44c4ff;
}
.chat-input{
    position:fixed;
    bottom: 0;
    width: 100%;
    border-top:1px solid #eee;
    background-color :#fff;
    padding-top: 10px;
    padding-bottom: 10px;
    left: 0;
    height: 30px;    
    padding-left: 5px;
}
.chat-input input{
    background-color : transparent;
    border: none;
    border-bottom: 1px solid #888;
    width: 85%;
    font-size: 1.2em;
}
.chat-input input:focus{
    outline:none;
}
.chat-input button{
    background-color : transparent;
    border: none;
    color : #e02100;
}
.border-red{
    border : 4px solid #e02100;
}
</style>




