<template>
  <div class="user-list">
      <div  class="loader">
           <img src="https://res.cloudinary.com/dxwtipc8n/image/upload/v1509920385/logo_reb47h.svg" alt="" class="logo">
      </div>
      <user-card v-for="user in users" :user="user"></user-card>
     
  </div>
</template>
<script>
import userCard from './user-card.vue'
import {mapGetters} from 'vuex';
import {Bus} from '../main'

export default {
  name : 'userList',
  data(){
      return{
          hasNoUsers : false
      }
  },
  components : {
      userCard : userCard
  },
  computed : {
      ...mapGetters({
          users : 'users'

      }),
     
  },
  mounted(){
       if(this.users.length === 0){
              this.hasNoUsers = true
      }
      Bus.$on('swipe' , ()=>{
          console.log(this.users.length)
          if(this.users.length === 0){
              this.hasNoUsers = true
          }
      })
  },
  created (){
      console.log(this.users.length)
      
  }
}
</script>
<style scoped>
.user-list{
    
    position: absolute;
    margin-top: 60px;
    overflow: hidden;
    width: 100%;
    height: 70%;
   
}
.user-list > user-card{
    box-shadow: 0px 0px 30px rgba(0,0,0,1)
}
.loader{
    text-align: center;
    position: absolute;
    height: 100%;
    width: 100%;
    top: 50%;
    margin-top: -100px;
}
.loader img{
    width: 200px;
    height: 200px;
}
.logo{
    animation-name: beating;
    animation-duration: 1.4s;
    animation-iteration-count:infinite;
}
@keyframes beating {
    0%{transform: scale(1)}
    10%{transform : scale(1.1)}
    20%{transform : scale(1)}
    80%{transform : scale(1.15)}
    100%{transform : scale(1)}
}
</style>



