<template>
    <div class="home">
    <nav-menu></nav-menu>
    <user-list></user-list>
    <control></control>
    <message></message>
    <matched v-show="isMatched" :user="user" :userMatch="userMatch"></matched>
    <userprofiles></userprofiles>
    <profile v-show="isProfileOpen" :user="profileUser"></profile>
    </div>
</template>
<script>
import axios from 'axios'
import { Bus } from '../main'
import {mapGetters} from 'vuex';


//components
import navMenu from './menu.vue'
import userList from './user.vue'
import control from './control.vue'
import matched from './matched.vue'
import message from './message.vue'
import userprofiles from './userprofiles.vue'
import profile from './profile.vue'

export default {
  name : 'home',
  data () {
      return{
          userMatch : {},
          user : {} ,
          isMatched : false,
          isMessage : false,
          profileUser : {},
          isProfileOpen: false,
      }
  },
  components : {
     navMenu : navMenu,
    userList : userList,
    control : control,
    matched : matched,
    message : message,
    userprofiles : userprofiles,
    profile
  },
  mounted(){
     
      if(localStorage.getItem('token')){
          this.$socket.emit('verifyToken' , localStorage.getItem('token'))
     }else{
         this.$router.push('/login')
     }

      Bus.$on('openProfile' , (user)=>{
          this.profileUser = user
          this.isProfileOpen = true
      })
      Bus.$on('closeProfile' , ()=>{
          this.profileUser = {}
          this.isProfileOpen = false
      })
  },
  ready:function(){

    window.onbeforeunload = this.leaving;
    window.onblur = this.leaving;
    window.onmouseout = this.leaving;

   
   },
  created(){
     
      
      Bus.$on('matched' , (payload)=>{          
          this.userMatch = payload.matched
          this.user = payload.user
          this.isMatched = payload.isMatched
      })
      Bus.$on('closeMatch' , ()=>{
          this.isMatched = false
      })
     
     
      Bus.$on('closeMessage' , ()=>{
          this.isMessage = false
      })
  },
  methods:{
      leaving(){
          console.log('leaaviinngg....')
          this.$socket.emit('saveUnreadMessages' , this.$store.state.newMessages) 
      }
  }
}
</script>
<style scoped>
.home{
    position: fixed;
    height: 100%;
    width:100%;
    max-width: 1024px;
    overflow-x:hidden;
}
</style>



