<template>
  <div class="nav-menu">
       <i  @click="showProfiles" style="position:absolute;left: 10%"  class="fa fa-user fa-lg" aria-hidden="true"></i>
      <img src="https://res.cloudinary.com/dxwtipc8n/image/upload/v1509920385/logo_reb47h.svg" alt="" class="logo">
      <i @click="logout" class="fa fa-sign-out" aria-hidden="true"></i>
  </div>
</template>
<script>
import {Bus} from '../main'
export default {
  name : 'navMenu',
  methods: {
      logout(){
          localStorage.clear();
          this.$router.push('/login')
      },
      showProfiles(){
          Bus.$emit('openProfiles')
      }
  }
}
</script>
<style scoped>
.nav-menu{
 font-size: 1.5em;
 position: absolute;
 width: 100%;
 height: 50px;
 box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
 background-color : #fff;
 top: 0;
 left: 0;
 z-index: 999;
}
.nav-menu img{
    margin: 0 auto;
    position:absolute;
    height: 100%;
    left: 45%;
}
.nav-menu i{
    position:absolute;
    right: 8%;
    top: 30%;
    font-size: 1em;
      color :#e02100;
}
</style>




